.predict {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e(box) {
    padding: rem(24px);
    background: $selago;
    border-radius: rem(30px);
  }
  @include e(item) {
    @include notlc() {
      margin-bottom: rem(24px);
    }
  }
  @include e(img) {
    @include img();
  }
  @include e(title) {
    margin-bottom: rem(16px);
  }
  @include e(text) {
    margin-bottom: rem(30px);
  }
  @include e(mark) {
    @include before() {
      background: $scooter;
      width: rem(12px);
      height: rem(12px);
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      border-radius: 50%;
      z-index: 3;

      @include nth-child(1) {
        @include before() {
          background: black !important;
        }
      }
    }

    padding-left: rem(20px);
    @include notlc() {
      margin-bottom: rem(8px);
    }
  }
}

