.fixed-header {
  background: $selago;
  box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.08);
  z-index: 50;
}

.btn {
  display: inline-block;
  background: $easternBlue;
  color: $white;
  padding: rem(14px) rem(32px);
  border-radius: 50px;
  @include font-size(16, 19);
  font-weight: 700;
}

.title {
  font-size: #{clamp-size(18px, 36px, 320px, 1270px)};
  line-height: #{clamp-size(27px, 50px, 320px, 1270px)};
  font-weight: $extra;
  color: $firefly;
}

.text {
  font-size: #{clamp-size(14px, 16px, 320px, 1270px)};
  line-height: #{clamp-size(22px, 25px, 320px, 1270px)};
  color: $limedSpruce;
  font-weight: $medium;
}

.green-line {
  @include after() {
    width: 100%;
    height: #{clamp-size(3px, 7px, 320px, 1270px)};
    left: 0;
    bottom: rem(-7px);
    background: $easternBlue;
    border-radius: rem(5px);
  }
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="file"],
input[type="number"],
input[type="date"],
textarea,
select,
option {
}

.swiper-button-prev,
.swiper-button-next {
  background: $easternBlue;
  width: rem(45px);
  height: rem(45px);
  border-radius: 50%;
  top: 93%;
}

.swiper-button-prev::after {
  @include transition(0.3);
  content: "\f060";
  @include color-opacity($white, 0.5);
  font-family: "FontAwesome";
  font-weight: 300;
  font-size: rem(20px);
}
.swiper-button-next::after {
  @include transition(0.3);
  content: "\f061";
  @include color-opacity($white, 0.5);
  font-family: "FontAwesome";
  font-weight: 300;
  font-size: rem(20px);
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: rem(20px);
}

.swiper-pagination-bullet-active {
  background: $easternBlue;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
.swiper-pagination-bullet {
  margin: 0 rem(8px);
}
