.inito {
    @include e (item) {
        flex-direction: row;
    }
    @include e (img) {
        padding-top: 40% !important;
        @include nth-child(2) {
            order: 0;
          }
        flex: 0 0 50%;
    }
    @include e (article) {
        padding: 0 rem(40px);
        flex: 0 0 50%;
    }
}