.reviews {
  @include e(slider) {
    padding-bottom: 0;
  }
  @include e(slide) {
    align-items: center;
    flex-direction: row;
  }
  @include e(left) {
    order: 0;
    flex: 0 0 15%;
  }
  @include e(right) {
    flex: 0 0 85%;
  }
}

.swiper-arrows {
  @include flex-c();
  gap: rem(128px);
  margin-top: rem(40px);
}

.swiper-button-prev,
.swiper-button-next {
  position: static;
  margin: 0;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: rem(12px);
}
