.inito {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e (title) {
    margin-bottom: rem(40px);
  }
  @include e(item) {
    @include flex();
    flex-direction: column;
    @include notlc() {
      margin-bottom: rem(40px);
    }
  }
  @include e(article) {
    @include notlc() {
      margin-bottom: rem(40px);
    }
  }
  @include e(img) {
    padding-top: 80% !important;
    @include imgH();
    @include nth-child(2) {
      order: -1;
    }
    overflow: hidden;
    border-radius: rem(24px);
    margin-bottom: rem(14px);
  }
  @include e(name) {
    margin-bottom: rem(16px);
  }
  @include e(text) {
    margin-bottom: rem(24px);
  }
  @include e(list) {
    margin-bottom: rem(24px);
  }
  @include e(point) {
    @include notlc() {
      margin-bottom: rem(8px);
    }
  }
}
