.header {
    @include e (logo) {
        display: block;
        @include img();
        width: #{clamp-size(80px, 105px, 320px, 1270px)};
    }
  @include e(nav) {
    width: auto;
    height: auto;
    @include flex();
    flex-direction: row;
    position: static;
    background: transparent;
  }
  @include e(btn) {
    background: $easternBlue;
    color: $white;
  }
}

.nav {
    @include e (list) {
        @include flex();
        gap: rem(40px);
        padding-right: rem(60px);
        margin-bottom: 0;
    }
  @include e(link) {
    text-transform: capitalize;
    @include font-size(16, 19);
    font-weight: $regular;
    color: $firefly;
  }
}

.burger__icon {
  display: none;
}
