.brands {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e(title) {
    text-transform: uppercase;
    color: $silver;
    font-size: #{clamp-size(14px, 16px, 320px, 1270px)};
    line-height: 22px;
    font-weight: $bold;
    text-align: center;
    margin-bottom: rem(30px);
  }
  @include e(logo) {
    @include flex-c();
    column-gap: 55px;
    row-gap: 30px;
    flex-wrap: wrap;
    margin-bottom: rem(80px);
  }
  @include e(img) {
    @include img();
  }
  @include e(item) {
    padding: rem(24px);
    background: $selago;
    text-align: center;
    border-radius: rem(24px);
    margin-bottom: rem(24px);
  }
  @include e(name) {
    margin-bottom: rem(16px);
  }
}
