.chart {
  margin-top: #{clamp-size(64px, 110px, 320px, 1270px)};
  background: $selago;
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e (left) {
    text-align: center;
    margin-bottom: rem(40px);
  }
  @include e(right) {
    @include img();
  }
  @include e(title) {
    font-size: #{clamp-size(24px, 48px, 320px, 1270px)};
    margin-bottom: rem(16px);
  }
  @include e(text) {
    margin-bottom: rem(24px);
  }
}
