.reviews {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e (slider) {
    padding-bottom: rem(80px);
  }
  @include e(slide) {
    overflow: hidden;
    @include before (182, 88) {
        left: 0;
        bottom: 0;
        background: url("../img/why__decor.svg") no-repeat;
        transform: scale(-1, 1);
    }
    padding: rem(24px);
    border-radius: rem(24px);
    background: $selago;
    color: $firefly;
    display: flex;
    flex-direction: column;
  }
  @include e (left) {
    order: 1;
  }
  @include e (right) {
    margin-bottom: rem(16px);
  }
  @include e(rating) {
    display: block;
    margin-bottom: rem(8px);
    span {
      font-size: rem(12px);
      color: $golden;
    }
  }
  @include e(name) {
    @include font-size(14, 17);
    font-weight: $semi;
    margin-bottom: rem(5px);
  }
  @include e (date) {
    color: $regentGray;
    @include font-size (12, 22);
    font-weight: $medium;
  }
  @include e(title) {
    @include font-size(18, 27);
    font-weight: $bold;
    margin-bottom: rem(16px);
  }
  @include e(text) {
    @include font-size(14, 22);
  }
}
