.btn {
  @include transition(0.3);
  @include hover() {
    background: $easternBlueDark;
  }
}
.swiper-button-prev,
.swiper-button-next {
  @include transition(0.3);
  @include hover() {
    background: $easternBlueDark;
  }
}

.swiper-button-prev:hover::after,
.swiper-button-next:hover::after {
  @include color-opacity($white, 1);
}

.footer {
  @include e(social-link) {
    cursor: pointer;
    @include hover() {
      --fill-icon: #1f94aa;
    }
  }
}
