.why {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e(title) {
    margin-bottom: rem(32px);
  }
  @include e(persone) {
    @include flex();
    margin-bottom: rem(40px);
  }
  @include e(img) {
    @include img();
    overflow: hidden;
    border-radius: 50%;
  }
  @include e(job) {
    padding-left: rem(15px);
  }
  @include e (profession) {
    font-weight: $semi;
  }
  @include e(right) {
    overflow: hidden;
    padding: rem(24px);
    background: $selago;
    border-radius: rem(30px);
    z-index: -1;
    @include before(182, 88) {
      right: 0;
      bottom: 0;
      background: url("../img/why__decor.svg") no-repeat;
    }
  }
  @include e(question) {
    margin-bottom: rem(24px);
  }
  @include e(article) {
    margin-bottom: rem(24px);
  }
  @include e(note) {
    color: $easternBlue;
    @include font-size(12, 22);
    font-weight: $medium;
  }
}
