.how {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e(title) {
    margin-bottom: rem(16px);
  }
  @include e(text) {
    margin-bottom: rem(24px);
  }
  @include e(btn) {
    margin-bottom: rem(24px);
  }
  @include e(note) {
    color: $silver;
    @include font-size(11, 18);
    font-weight: $medium;
    margin-bottom: rem(32px);
  }
  @include e(item) {
    @include notlc() {
      margin-bottom: rem(24px);
    }
  }
  @include e(img) {
    overflow: hidden;
    border-radius: rem(24px);
    margin-bottom: rem(24px);
    @include img();
  }
  @include e(box) {
    @include flex();
  }
  @include e(num) {
    @include flex-c();
    width: rem(60px);
    height: rem(60px);
    flex-shrink: 0;
    border-radius: 50%;
    color: $firefly;
    @include font-size(24, 32);
    font-weight: $bold;
    background: $cornFlower;
  }
  @include e(name) {
    padding-left: rem(15px);
  }
}
