.faq {
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e(left) {
    margin-bottom: rem(24px);
  }
  @include e(item) {
    cursor: pointer;
    background: $selago;
    border-radius: rem(24px);
    padding: rem(24px);
    @include notlc() {
      margin-bottom: rem(16px);
    }
  }
  @include e(name) {
    padding-right: rem(30px);
    @include after() {
      content: "\f068";
      right: rem(20px);
      font-family: "Font Awesome 5 free";
      font-size: rem(20px);
    }
    @include font-size(16, 26);
    font-weight: $bold;
    margin-bottom: rem(12px);
    &.active {
        @include transition(0.3);
        @include after() {
          content: "\2b";
          right: rem(20px);
          font-family: "Font Awesome 5 free";
        }
      }
  }
  @include e(text) {
    @include font-size(14, 22);
    font-weight: $medium;
    overflow: hidden;
    max-height: 0;
    @include transition(0.3);
  }
}
