.footer {
  color: $white;
  background: $firefly;
  @include e(container) {
    padding-top: #{clamp-size(32px, 50px, 320px, 1270px)};
    padding-bottom: #{clamp-size(32px, 50px, 320px, 1270px)};
  }
  @include e(statistic) {
    @include flex-sb();
    flex-wrap: wrap;
  }
  @include e(inner) {
    @include borderopacity-b(1px, $white, 0.1);
    flex: 0 0 100%;
    padding-bottom: rem(16px);
  }
  @include e(logo) {
    padding: rem(10px) 0;
  }

  @include e(social) {
    @include flex();
    gap: rem(15px);
  }
  @include e(stat) {
    @include flex();
    @include notlc() {
      margin-bottom: rem(16px);
    }
  }
  @include e(contact) {
    @include borderopacity-t(1px, $white, 0.1);
    padding-top: rem(30px);
    flex-grow: 1;
  }
  @include e(col) {
    margin-bottom: rem(15px);
  }
  @include e(icon) {
    flex-shrink: 0;
    @include flex-c();
    border-radius: 50%;
    background: $easternBlue;
    width: rem(48px);
    height: rem(48px);
  }
  @include e(name) {
    padding-left: rem(16px);
  }

  @include e(title) {
    @include font-size(15, 19);
    font-weight: $bold;
    margin-bottom: rem(24px);
    @include after() {
      content: "\f107";
      right: rem(15px);
      font-family: "Font Awesome 5 free";
      color: $white;
      @include transition(0.3);
    }
    &.active {
      @include after() {
        content: "\f107";
        right: 0;
        font-family: "Font Awesome 5 free";
        transform: rotate(90deg);
      }
    }
  }
  @include e(list) {
    overflow: hidden;
    max-height: 0;
    @include transition(0.3);
  }
  @include e(item) {
    @include notlc() {
      margin-bottom: rem(16px);
    }
  }
  @include e (social-link) {
    --fill-icon: #ffffff;
  }
  @include e(link) {
    @include color-opacity($white, 0.8);
    font-weight: $medium;
  }
  @include e(copy) {
    padding-top: rem(15px);
    @include borderopacity-t(1px, $white, 0.1);
    @include color-opacity($white, 0.5);
    font-size: #{clamp-size(11px, 14px, 320px, 1270px)};
    line-height: rem(17px);
  }
  @include e(text) {
    margin-bottom: rem(30px);
  }
}
