.footer {
  @include e(statistic) {
    
  }
  @include e(inner) {
    flex: 0 0 80%;
    @include flex();
    gap: rem(30px);
    border-bottom: none;
  }
  @include e(stat) {
    @include notlc() {
      margin-bottom: 0;
    }
  }

  @include e(logo) {
    padding-top: rem(15px);
    align-self: flex-start;
    order: 1;
  }
  @include e(social) {
    flex: 0 0 20%;
  }
  @include e(title) {
    @include after() {
      content: none;
    }
  }
  @include e(contact) {
    border-top: none;
    padding-top: rem(15px);
    flex-grow: 0;
    @include flex();
    order: 1;
  }
  @include e(col) {
    padding: 0 rem(15px);
    flex: 0 0 33%;
    align-self: flex-start;
  }
  @include e(list) {
    overflow: visible;
    max-height: 100%;
  }
}
