.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  @include e(container) {
    @include flex-sb();
    padding-top: #{clamp-size(15px, 32px, 320px, 1270px)};
    padding-bottom: #{clamp-size(15px, 32px, 320px, 1270px)};
  }
  @include e(nav) {
    position: absolute;
    width: 100%;
    height: 100vh;
    left: -100%;
    top: 0;
    background: $easternBlue;
    @include flex-c();
    flex-direction: column;
    @include transition(0.3);
    z-index: 50;
    &.active {
      left: 0;
    }
  }
  @include e(btn) {
    text-transform: uppercase;
    background: $white;
    color: $easternBlue;
  }
}

.nav {
  @include e(list) {
    text-align: center;
    margin-bottom: rem(20px);
  }
  @include e(link) {
    text-transform: uppercase;
    color: $white;
    @include font-size(25, 50);
    font-weight: $semi;
  }
}


.burger__icon {
  @include burger();
}
